import React, { ReactElement, useState, useEffect, useCallback, useMemo, useReducer } from 'react';
import url from 'url';
import styles from '../css/pairings.module.css';
import componentStyles from '../css/components.module.css';
import { TournamentStatus, ID, Player, Result } from '../interfaces';
import { GET, POST, server } from '../utils/requests';
import { AiOutlineMinus, AiOutlineClose } from 'react-icons/ai';
import { TournamentSettings } from '../resources/settings';

interface PairingsProps {
	userRound: number

	status: TournamentStatus
	playerDict: Map<ID, Player>
	updatePlayerDict: () => void
	playerDictKey: string
	settings: TournamentSettings
	triggerConfirm: (message: string, resolve: () => void, reject: () => void) => void
	id: string
	switchPairings: () => void
	updateTournamentStatus: () => void
	updateTournamentSettings: () => void
	setDisplayPlayer: (id: string) => void
	filtered: string[]
}

export default function Switch(props: PairingsProps): ReactElement {

	const round = useMemo(() => props.status.round, [props.status.round]);

	const [pairingData, setPairings] = useState([] as Result[]);
	const [selected, setSelected] = useState([]);
	const [select, deselect] = [
		useCallback((id: string) => selected.length < 2 && !selected.includes(id) ? setSelected([...selected, id]) : null, [selected, setSelected]),
		useCallback((id: string) => {
			if (!selected.includes(id)) return;
			let action = selected.slice(0);
			action.splice(selected.indexOf(id), 1);
			return setSelected(action);
		}, [selected, setSelected])
	];

	const getPairings = useCallback((): Promise<void> => {
		if (!round) return;
		if (!props.playerDict.size) return;
		return GET({ url: '/tournament/' + props.id + '/getPairings' })
			.then((pairings: Result[]) => {
				setPairings(pairings);
			})
			.catch(console.error);
	}, [props.id, setPairings, round, props.playerDict, props.playerDictKey]);

	const getDisplayName = useCallback((id: ID): string => {
		if (id === 'bye') return 'BYE';
		let player = props.playerDict.get(id);
		if (!player) return id;
		let displayName: string;
		if (props.settings.competitors === 'individual') displayName = [player.firstName, player.lastName].join(' ');
		else displayName = player.firstName;
		return `${displayName} [${player.score}]`;
	}, [props.playerDict]);

	const undoRoundPairings = useCallback(() => {
		props.triggerConfirm(
			'Really undo all pairings for this round?',
			() => POST({
				url: url.resolve(server, ['tournament', props.status.id, 'undoRoundPairings'].join('/')),
				data: {
					round: props.status.round
				}
			}).then(() => {
				setSelected([]);
				props.updateTournamentStatus();
				props.updatePlayerDict();
				if (props.settings.publishPairings) props.updateTournamentSettings();
				props.switchPairings();
			}),
			() => setSelected([])
		);
	}, [setSelected, props.status.id, props.status.round, props.settings.publishPairings, props.updateTournamentSettings]);

	const triggerPairingSwap = useCallback(() => {
		props.triggerConfirm(
			`Really swap pairings ${getDisplayName(selected[0])} and ${getDisplayName(selected[1])}?`,
			() => POST({
				url: url.resolve(server, ['tournament', props.status.id, 'switchPairings'].join('/')),
				data: {
					swapX: selected[0],
					swapY: selected[1],
					round: props.status.round
				}
			}).then(() => {
				setSelected([]);
				props.updatePlayerDict();
			}),
			() => setSelected([])
		);
	}, [selected, setSelected, props.status.id, props.status.round]);

	const destroyPairing = useCallback((id: string, idW: string, idB: string) => {
		props.triggerConfirm(
			`Really remove pairing ${getDisplayName(idW)} vs. ${getDisplayName(idB)}?`,
			() => POST({
				url: url.resolve(server, ['tournament', props.status.id, 'destroyPairing'].join('/')),
				data: { id, round }
			}).then(() => {
				setSelected([]);
				props.updatePlayerDict();
			}),
			() => {}
		);
	}, [getDisplayName, round, setSelected, props.updatePlayerDict]);

	function UndoButton(): ReactElement {
		return (
			<div
				className={[styles.undoButton].join(' ')}
				onClick={undoRoundPairings}
			>
				Undo all pairings
			</div>
		);
	}

	function NextButton(): ReactElement {
		let left = 2 - selected.length;
		return left ? (
			<div className={[styles.waitButton].join(' ')} >
				{`Select ${left} more pairing${left > 1 ? 's' : ''}`}
			</div>
		) : (
			<div
				className={[styles.nextButton].join(' ')}
				onClick={triggerPairingSwap}
			>
				Swap pairings
			</div>
		);
	}

	function Listing(): ReactElement[] {
		let pairings = [] as ReactElement[];
		for (let r of pairingData) {
			let { id, idW, idB } = r;
			let [wDisplay, bDisplay] = [getDisplayName(idW), getDisplayName(idB)];
			let hidden = props.filtered && props.filtered.length && !props.filtered.some(p => idW === p || idB === p);
			pairings.push(
				<div id={id} className={styles.pairing} key={['pairings', idW, idB].join('.')} style={hidden ? {display: 'none'} : {}}>
					<div
						className={['playerInfo', styles.playerInfo, idW === 'bye' ? styles.bye : ''].join(' ')}
						id={idW}
						onContextMenu={(e) => {
							e.preventDefault();
							props.setDisplayPlayer(idW);
						}}
						onClick={() => !selected.includes(idW) ? select(idW) : deselect(idW)}
						style={{
							backgroundColor: selected.includes(idW) ? 'darkblue' : ''
						}}
					>{wDisplay}</div>
					<div className={componentStyles.resultsBridge}>
						<div className={componentStyles.setResult}>{'\u200b'}</div>
						<button tabIndex={-1} className={componentStyles.reject} onClick={() => destroyPairing(id, idW, idB)}>
							<AiOutlineClose />
						</button>
						<div className={componentStyles.setResult}>{'\u200b'}</div>
					</div>
					<div
						className={['playerInfo', styles.playerInfo, styles.playerInfo, idB === 'bye' ? styles.bye : ''].join(' ')}
						id={idB}
						onContextMenu={(e) => {
							e.preventDefault();
							props.setDisplayPlayer(idB);
						}}
						onClick={() => !selected.includes(idB) ? select(idB) : deselect(idB)}
						style={{
							backgroundColor: selected.includes(idB) ? 'darkblue' : ''
						}}
					>{bDisplay}</div>
				</div>
			);
		}
		return pairings;
	}

	useEffect(() => {
		getPairings();
	}, [getPairings]);

	const listing = useMemo(Listing, [selected, pairingData, getDisplayName, props.updatePlayerDict, getPairings, props.playerDictKey]);

	return (
		<>
			<div className={styles.pairings}>
				<div className={styles.titleContainer}>
					<div className={['topBanner', styles.topBanner].join(' ')}>
						Switch Pairing Mode
					</div>
				</div>
				<div className={[styles.pairingContainer, 'scrollable'].join(' ')}>
					<div className={[styles.pairingWrapper].join(' ')}>
						{listing}
					</div>
				</div>
			</div>
			<div className={['bottomBannerBackground', styles.statusBarBackground].join(' ')}>
				<div className={['bottomBanner', styles.switchStatusBar].join(' ')}>
					{round ? <UndoButton /> : null}
					<NextButton />
				</div>
			</div>
		</>
	);

}