import React, { ReactElement, useState, useEffect, useCallback, useMemo, useReducer } from 'react';
import styles from '../css/pairings.module.css';
import { TournamentStatus, ID, Player, Result, ResultObject, GameLinks, Fetched, Game } from '../interfaces';
import { SinglePairing } from '../components/';
import { GET, POST } from '../utils/requests';
import { AiOutlineLoading3Quarters } from 'react-icons/ai';
import { PairingDetails, ManualPairingForm } from '../components/SinglePairing';
import { TournamentSettings } from '../resources/settings';
import { FaBars } from 'react-icons/fa';
import { convertPoolDisplay } from '../utils/prototype';

interface PairingsProps {
	userRound: number
	status: TournamentStatus
	playerDict: Map<ID, Player>
	updatePlayerDict: () => void
    playerDictKey: string
	settings: TournamentSettings
	updateStatus: () => void
	updateTournamentSettings: () => void
	updateSetting: (k: string, v: any) => Promise<void>
	id: string
	canTriggerNextRound: boolean
	setDisplayPlayer: (id: string) => void
	toggleSidebar: () => void
	toggleRightTab: () => void

	updateRound: () => void
	setLastRound: (override: boolean) => void
	toggleCrossTable: () => void
	lastRoundOverride: boolean
	triggerConfirm: (message: string, resolve: () => void, reject: () => void) => void
	filtered: string[]
}

export default function Pairings(props: PairingsProps): ReactElement {

	const round = useMemo(() => props.userRound || props.status.round, [props.userRound, props.status.round]);
    
	const [resultsState, updateResults] = useReducer((state: string, action: {}) => Math.random().toString(16).slice(2, 9), '');
	const reduceResults = useCallback((state: Map<string, ResultObject>, {id, W, B, gameLinks, fetched, games}: {
		id: string
		W: Player,
		B: Player,
		gameLinks: GameLinks,
		fetched?: Partial<Fetched>,
		games?: Partial<Game>[]
	}): Map<string, ResultObject> => {
		let scoreW = null;
		if (!W) scoreW = 0;
		else if (W.histories && W.histories[round] && W.histories[round][props.settings.displayPoints]) scoreW = W.histories[round][props.settings.displayPoints];
		let scoreB = null;
		if (!B) scoreB = 0;
		else if (B.histories && B.histories[round] && B.histories[round][props.settings.displayPoints]) scoreB = B.histories[round][props.settings.displayPoints];
		state.set(id, [
			scoreW,
			scoreB,
			gameLinks,
			fetched,
			games
		]);
		updateResults({});
		return state;
	}, [round, props.settings.displayPoints]);

	const [pairingData, setPairings] = useState([] as Result[]);
	const [results, setResults] = useReducer(reduceResults, new Map() as Map<string, ResultObject>);
	const [loading, setLoading] = useState(false);

	const getPairings = useCallback((): Promise<void> => {
		if (!round) return;
		if (!props.playerDict.size) return;
		return GET({
			url: '/tournament/' + props.id + '/getPairings',
			params: { round }
		})
			.then((pairings: Result[]) => {
				for (let r of pairings) {
					let { id, idW, idB, fetched, games } = r;
					let gameLinks = { link: r.link, source: r.linkSource, type: r.linkType };
					let [W, B] = [props.playerDict.get(idW), props.playerDict.get(idB)];
					setResults({
						id, 
						W,
						B,
						gameLinks,
						fetched,
						games
					});
				}
				setPairings(pairings);
			})
			.catch(console.error);
	}, [props.id, setPairings, setResults, round, props.playerDict, props.playerDictKey]);

	function getDisplayName(id: ID, player: Player): string {
		if (id === 'bye') return 'BYE';
		if (!player) return id;
		let displayName: string;
		if (props.settings.competitors === 'individual') displayName = [player.firstName, player.lastName].join(' ');
		else displayName = player.firstName;
		return `${displayName} [${player.score}]`;
	}
	
	let pending = Array.from(results.values())
		.filter(arr => arr.slice(0, 2).every(v => v === null));
			
	const pairRemaining = useCallback((): Promise<void> => {
		return POST({
			url: '/tournament/' + props.id + '/getRestPairings'
		})
			.then(props.updatePlayerDict)
			.catch(console.error);
	}, [getPairings, props.id]);
			
	const triggerNextRound = useCallback((): Promise<void> => {
		setLoading(true);
		return POST({
			url: '/tournament/' + props.id + '/getNextPairings'
		})
			.then(props.settings.publishPairings ? props.updateTournamentSettings : () => {})
			.then(props.updatePlayerDict)
			.then(props.updateRound)
			.catch(console.error)
			.finally(() => setLoading(false));
	}, [setLoading, setPairings, props.id, getPairings, props.updateRound, setLoading]);

	const canPairMore = useMemo((): boolean => {
		let length = Array.from(props.playerDict.values()).filter(p => p.active !== false && p.histories && !p.histories[round]).length;
		return !!length;
	}, [props.playerDict, props.playerDictKey, round]);

	function generatePairButton(): ReactElement {
		return (
			<div
				className={[styles.nextButton, 'button'].join(' ')}
				onClick={() => props.triggerConfirm('Pair remaining players for this round?', pairRemaining, () => {})}
			>
				Pair Remaining
			</div>
		);
	}

	const nextButtonState: 'skeleton' | 'loading' | 'none' | 'inactive' | 'wait' | 'publish' | 'publish-final' | 'last' | 'next' = useMemo(() => {
		if (!props.settings?.pairingSystem) return 'skeleton';
		let lastRound: boolean;
		if (round >= props.settings.totalRounds) lastRound = true;
		else if (typeof props.lastRoundOverride !== 'undefined') lastRound = props.lastRoundOverride;
		else lastRound = false;
		if (loading) return 'loading';
		if (round < props.status.round) return 'none';
		if (!props.settings.isPublished) return 'publish';
		if (!props.canTriggerNextRound) return 'inactive';
		if (pending.length > 0 && props.settings.pairingSystem === 'swiss') return 'wait';
		if (lastRound) {
			if (props.status.active) return 'publish-final';
			return 'last';
		}
		return 'next';
	}, [round, props.status.active, props.settings.totalRounds, props.lastRoundOverride, loading, props.status.round, props.canTriggerNextRound, pending.length]);

	const setStatus = useCallback((set: boolean) => {
		POST({ url: ['tournament', props.id, set ? 'unpublish' : 'publish'].join('/') })
			.then(props.updateStatus);
		if (!set && props.settings.allowNewPlayers) props.updateSetting('allowNewPlayers', false);
	}, [props.id, props.settings.allowNewPlayers, props.updateSetting]);

	function generateNextButton(): ReactElement {
		switch (nextButtonState) {
		case 'skeleton':
			return <div className={styles.waitButton} />;
		case 'loading':
			return <div className={[styles.nextButton, 'button'].join(' ')}>
				<AiOutlineLoading3Quarters />
			</div>;
		case 'none':
			return <div className={[styles.waitButton].join(' ')} />;
		case 'inactive':
			return <div className={[styles.waitButton].join(' ')}>
				Must have at least 2 active players
			</div>;
		case 'publish':
			return <div className={[styles.nextButton, 'button'].join(' ')} onClick={() => props.updateSetting('isPublished', true)}>
				Publish draw
			</div>;
		case 'wait':
			return <div className={[styles.waitButton].join(' ')} >
				{`Awaiting ${pending.length} result${pending.length > 1 ? 's' : ''}`}
			</div>;
		case 'publish-final':
			return <div className={[styles.nextButton, 'button'].join(' ')} onClick={() => {
				setStatus(false);
				if (!props.settings.public) props.toggleCrossTable();
			}}>
				{props.settings.public ? 'Publish results' : 'Final Standings'}
			</div>;
		case 'last':
			return <div className={[styles.finishButton, 'button'].join(' ')} onClick={() => {	
				setStatus(true);
			}}>
				{props.settings.public ? 'Unpublish results' : 'Resume tournament'}
			</div>;
		case 'next':
			return <div className={[styles.nextButton, 'button'].join(' ')} onClick={triggerNextRound}>
				{props.status.round ? 'Next Round' : 'First round pairings'}
			</div>;
		}
	}
	
	function validateForm(form: HTMLFormElement): boolean | string | void {
		if (form.resultWhite.value === '' && form.resultBlack.value === '') return undefined;
		if (!Array.from(form).every((input) => (input as HTMLInputElement).validity.valid)) return false;
		let nW = Number(form.resultWhite.value);
		let nB = Number(form.resultBlack.value);
		if (nW + nB === 0) return 'Cannot have a 0-0 result';
		if (!props.settings.gamePointTotal) return true;
		let total = props.settings.displayPoints === 'game' ? props.settings.gamePointTotal : 1;
		if (nW + nB === total) return true;
		return 'Setting: sum results to ' + props.settings.gamePointTotal;
	}
    
	const [expanded, setExpanded] = useState('');

	const ManualPairing = useCallback(function ManualPairing(): ReactElement {
		const [edit, setEdit] = useState(false);

		if (!edit) return (
			<div className={styles.pairingAddButton} onClick={() => setEdit(true)}>
				New manual pairing
			</div>
		);
		return (
			<div className={styles.manualPairing}>
				<ManualPairingForm
					tournamentID={props.id}
					playerDict={props.playerDict}
					settings={props.settings}
					close={() => setEdit(false)}
					round={round}
					handleSubmit={async (e) => {
						updateResults({});
						props.updatePlayerDict();
						return e;
					}}
				/>
			</div>
		);
	}, [props.id, props.playerDict, props.settings, updateResults, props.updatePlayerDict]);

	const Listing = useCallback(function Listing(): ReactElement[] {
		let pairings = [] as ReactElement[];
		let dict = props.playerDict;
		let i = 1;
		let pools = [] as Result[][];
		for (let r of pairingData) {
			let pool = r.pool || 0;
			if (!props.settings.usingPools) pool = 0;
			if (!pools[pool]) pools[pool] = [] as Result[];
			pools[pool].push(r);
		}
		for (let j = 0; j < pools.length; j++) {
			if (!pools[j]) continue;
			if (j) pairings.push(
				<div id={['pool', j].join('.')} key={['pool', j].join('.')} className={styles.pool}>
					Pool {convertPoolDisplay(j, props.settings.poolDisplay)}
				</div>
			);
			for (let r of pools[j]) {
				let { id, idW, idB } = r;
				let [white, black] = [dict.get(idW), dict.get(idB)];
				let [wDisplay, bDisplay] = [getDisplayName(idW, white), getDisplayName(idB, black)];
				let result = results.get(id);
				if (!result) continue;
				let gameLinks = { link: r.link, source: r.linkSource, type: r.linkType };

				let hidden = props.filtered && props.filtered.length && !props.filtered.some(p => idW === p || idB === p);
				pairings.push(
					<div id={id} className={styles.pairing} key={['pairings', id].join('.')} style={hidden ? {display: 'none'} : {}}>
						<div
							className={['playerInfo', styles.playerInfo, idW === 'bye' ? styles.bye : ''].join(' ')}
							id={idW}
							onClick={(e: React.MouseEvent<HTMLDivElement>) => idW === 'bye' ? null : props.setDisplayPlayer((e.target as HTMLDivElement).id)}
						>{wDisplay}</div>
						<SinglePairing
							expand={(set: boolean) => setExpanded(set ? id : '')}
							expanded={expanded === id}
							result={result}
							gameLinks={gameLinks}
							tournamentID={props.id}
							inputClassName={styles.inputResult}
							hidden={{
								W: idW,
								B: idB,
								round,
								override: true
							}}
							validate={validateForm}
							handleSubmit={async (e) => {
								let form = e.target as HTMLFormElement & {resultWhite: HTMLInputElement, resultBlack: HTMLInputElement};
								results.set(id, [Number(form.resultWhite.value), Number(form.resultBlack.value), {}]);
								if (form) {
									let parent = form.parentElement as HTMLDivElement;
									if (parent) {
										let nextSibling: HTMLDivElement = parent;
										do {
											nextSibling = nextSibling.nextSibling as HTMLDivElement;
										} while (nextSibling && !nextSibling.getElementsByTagName('form')[0]);
										if (nextSibling) {
											let newForm = nextSibling.getElementsByTagName('form')[0] as HTMLFormElement;
											let newInput = newForm.getElementsByTagName('input')[0] as HTMLInputElement;
											if (newInput) newInput.focus();
										}
									}
								}
								updateResults({});
								props.updatePlayerDict();
								return e;
							}}
						/>
						<div
							className={['playerInfo', styles.playerInfo, styles.playerInfo, idB === 'bye' ? styles.bye : ''].join(' ')}
							id={idB}
							onClick={(e: React.MouseEvent<HTMLDivElement>) => idB === 'bye' ? null : props.setDisplayPlayer((e.target as HTMLDivElement).id)}
						>{bDisplay}</div>
					</div>,
					<PairingDetails
						key={['pairingDetails', id].join('.')}
						tournamentID={props.status.id}
						id={id}
						board={i}
						round={round}
						className={['dropdown', styles.pairing].join(' ')}
						style={hidden ? {display: 'none'} : {}}
						result={result}
						expanded={expanded === id}
						gameLinks={gameLinks}
						individual={props.settings.competitors === 'individual'}
						update={getPairings}
						updatePlayerDict={props.updatePlayerDict}
						triggerConfirm={props.triggerConfirm}
						settings={props.settings}
					/>
				);
				i++;
			}
		}
		return pairings;
	}, [props.playerDict, pairingData, getDisplayName, results, resultsState, props.updatePlayerDict, getPairings, props.setDisplayPlayer, setResults, props.playerDictKey]);

	useEffect(() => {
		getPairings();
	}, [getPairings]);

	const listing = useMemo(Listing, [Listing]);

	return (
		<>
			<div className={styles.pairings}>
				<div className={styles.stage}>
					<div className={styles.titleContainer}>
						<div className={['topBanner', styles.topBanner].join(' ')}>
							{nextButtonState === 'skeleton' ? null : 'Pairings'}
						</div>
					</div>
					<div className={[styles.pairingContainer, 'scrollable'].join(' ')}>
						<div className={[styles.pairingWrapper].join(' ')}>
							{listing}
							{canPairMore && round ? <ManualPairing /> : null}
						</div>
					</div>
				</div>
			</div>
			<div className={['bottomBannerBackground', styles.statusBarBackground].join(' ')}>
				<div className={['bottomBanner', styles.statusBar].join(' ')}>
					<div className={['button'].join(' ')} onClick={props.toggleSidebar}>
						{nextButtonState === 'skeleton' ? null : <FaBars />}
					</div>
					<div style={{display: 'grid', gridAutoFlow: 'column', gridAutoColumns: '1fr', width: '100%', columnGap: '5px'}}>
						{canPairMore && props.status.round ? generatePairButton() : null}
						{generateNextButton()}
					</div>
					<div className={['button'].join(' ')} onClick={props.toggleRightTab}>
						{nextButtonState === 'skeleton' ? null : <FaBars />}
					</div>
				</div>
			</div>
		</>
	);

}